.navbar {
    display: flex;
    background-color: #f6f6f6;
}

.nav {
    display: flex;
    padding: 1rem;
    align-items: center;
    justify-content: right;
    width: 100%;
    padding-right: 10rem;
  }
  
  .nav-link {
    color: gray;
    text-decoration: none;
    margin-right: 1rem;
    padding: 0.5rem 3rem;
    font-family: Nexa;
    font-size: 1.2rem;
  }
  
  .nav-link:hover {
    text-decoration: underline;
  }
  
  /* Apply styles to the active link */
  .active {
    text-decoration: underline;
  }

  .logo {
    padding: 1rem;
    padding-left: 3rem;
    width: 150px;
    height: 150px;
    margin-right: 1rem;
  }
  